                                CH: "原委辦單位編號",
                                EN: "Number of the original agency"
                            },
                            setting: {
                                require: true
                            }
                        },
                        {
                            type: "text",
                            label: { CH: "計劃名稱", EN: "Droplist" },
                            des: {},
                            setting: {}
                        },
                        {
                            type: "timepicker",
                            label: { CH: "計劃名稱", EN: "timepicker" },
                            des: {},
                            setting: {}
                        },
                        {
                            type: "datepicker",
                            label: { CH: "計劃名稱", EN: "datepicker" },
                            des: {},
                            setting: {}
                        }
                    ]
                },
                {
                    label: { CH: "名稱2", EN: "DIV2" },
                    items: [
                        {
                            type: "table",
                            label: { CH: "計畫內容", EN: "Table" },
                            des: {},
                            setting: {
                                editable: true,
                                addable: true
                            },
                            tableSn: 1
                        },
                        {
                            type: "text",
                            label: { CH: "計劃名稱", EN: "Droplist" },
                            des: {},
                            setting: {}
                        }
                    ]
                }
            ]
        };
    },
    computed: {
        filterInsertContents() {
            return this.defaultInsertContents.filter(searchResult =>
                searchResult.label.CH.match(this.searchWords)
            );
        }
    },
    mounted() {},
    methods: {
        removeAt(divIndex, itemIndex) {
            this.defaultContents[divIndex].items.splice(itemIndex, 1);
            this.currentEdit = {};
        },
        log(evt) {
            console.log("log", evt);
        },

        onChoose() {
            // console.log("choose");
        },
        onUnchoose() {
            // console.log("unchoose");
        },
        onBlur() {
            // console.log("onblur");
            // setTimeout(() => {
            //     this.closeEdit();
            // }, 100);
        },
        onClone(evt) {
            // 改寫 clone 為 deep copy 修改值才不會變更原本的
            return { ...evt };
        },
        editContent(content, divIndex, itemIndex) {
            this.closeEdit();
            if (divIndex != undefined) {
                this.editDiv = false;
                this.$set(
                    this.defaultContents[divIndex].items[itemIndex],
                    "editorEnable",
                    true
                );
            } else {
                this.editDiv = true;
            }
            this.currentEdit = content;
        },
        closeEdit() {
            this.defaultContents.forEach((div, divIndex) => {
                this.defaultContents[divIndex].items.forEach(
                    (item, itemIndex) => {
                        this.$set(
                            this.defaultContents[divIndex].items[itemIndex],
                            "editorEnable",
                            false
                        );
                    }
                );
            });
        },
        addDIV() {
            this.defaultContents.push({
                label: { CH: "名稱", EN: "DIV" },
                items: []
            });
        },
        changeI18n(lang) {
            this.i18n = lang;
        }
    }
};
</script>
